import React from "react"
import { graphql, Link } from "gatsby"
import { MDXProvider } from "@mdx-js/react"

import Container from "../../components/container"
import Section from "../../components/section"
import Layout from "../../components/layouts/layout"
import { LazyPlot } from '../../components/Plotly'

const components = {
  Link, LazyPlot
}

function player(gamelog, home=true) {
  if (home) {
    return gamelog.homeTeamPlayerStaffName + '[' + gamelog.homeTeamPlayerStaffNrDesc + ']'
  }
  return gamelog.awayTeamPlayerStaffName + '[' + gamelog.awayTeamPlayerStaffNrDesc + ']'
}

function parseGamelog(gamelogJson) {
  const colors = [`red`, `green`]

  let toreH = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    name: 'Tore Heim',
    mode: 'lines+markers',
    marker: {
      size: []
    },
    line: {
      color: 'rgb(234, 153, 153)',
      width: 2
    }
  }
  let toreA = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    name: 'Tore Gast',
    mode: 'lines+markers',
    marker: {
      size: []
    },
    line: {
      color: 'rgb(164, 194, 244)',
      width: 2
    }
  }
  let strafenH = {
    type: 'bar',
    name: 'Strafen Heim',
    x: [],
    y: [],
    text: [],
    width: [],
    marker: {
      color: 'rgba(234, 153, 153, 0.3)',
      size: 12
    }
  }
  let strafenA = {
    type: 'bar',
    name: 'Strafen Gast',
    x: [],
    y: [],
    text: [],
    width: [],
    marker: {
      color: 'rgba(164, 194, 244, 0.3)',
      size: 12
    }
  }
  let techH = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    mode: 'markers',
    name: 'Aktionen Heim',
    marker: {
      symbol: [],
      color: 'rgba(234, 153, 153, 0.6)',
      size: 12
    }
  }
  let techA = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    mode: 'markers',
    name: 'Aktionen Gast',
    marker: {
      symbol: [],
      color: 'rgba(164, 194, 244, 0.6)',
      size: 12
    }
  }
  let eventH = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    mode: 'markers',
    name: 'Timeout Heim',
    marker: {
      symbol: [],
      color: 'rgb(234, 153, 153)',
      size: 12
    }
  }
  let eventA = {
    x: [],
    y: [],
    text: [],
    type: 'scatter',
    mode: 'markers',
    name: 'Timeout Gast',
    marker: {
      symbol: [],
      color: 'rgb(164, 194, 244)',
      size: 12
    }
  }

  gamelogJson.data.gameLog.map((gamelog, index) => {
    let results = gamelog.result.split(':')
    if (gamelog.homeTeamActionLCID === 'bFeldTor' || gamelog.homeTeamActionLCID === 'b7mTor') {
      toreH['x'].push(gamelog.timeInt)
      toreH['y'].push(results[0])
      toreH['text'].push(gamelog.actionText + ': ' + player(gamelog, true))
      if (gamelog.homeTeamActionLCID === 'b7mTor') {
        toreH['marker']['size'].push(15)
      } else {
        toreH['marker']['size'].push(10)
      }
    }
    if (gamelog.awayTeamActionLCID === 'bFeldTor' || gamelog.awayTeamActionLCID === 'b7mTor') {
      toreA['x'].push(gamelog.timeInt)
      toreA['y'].push(results[1])
      toreA['text'].push(gamelog.actionText + ': ' + player(gamelog, false))
      if (gamelog.homeTeamActionLCID === 'b7mTor') {
        toreA['marker']['size'].push(15)
      } else {
        toreA['marker']['size'].push(10)
      }
    }
    if (gamelog.homeTeamActionLCID === 'b2Min' || gamelog.homeTeamActionLCID === 'bVerwarnung') {
      strafenH['x'].push(gamelog.timeInt)
      if (gamelog.homeTeamActionLCID === 'b2Min') {
        strafenH['y'].push(15)
        strafenH['width'].push(10)
        strafenH['text'].push('2": ' + player(gamelog, true))

        strafenH['x'].push(gamelog.timeInt + 120)
        strafenH['y'].push(15)
        strafenH['width'].push(10)
        strafenH['text'].push('2" Ende')
      } else {
        strafenH['y'].push(10)
        strafenH['width'].push(10)
        strafenH['text'].push(gamelog.actionText + ' ' + player(gamelog, true))
      }
    }
    if (gamelog.awayTeamActionLCID === 'b2Min'|| gamelog.awayTeamActionLCID === 'bVerwarnung') {
      strafenA['x'].push(gamelog.timeInt)
      if (gamelog.awayTeamActionLCID === 'b2Min') {
        strafenA['y'].push(15)
        strafenA['width'].push(10)
        strafenA['text'].push('2": ' + player(gamelog, false))

        strafenA['x'].push(gamelog.timeInt + 120)
        strafenA['y'].push(15)
        strafenA['width'].push(10)
        strafenA['text'].push('2" Ende')
      } else {
        strafenA['y'].push(10)
        strafenA['width'].push(10)
        strafenA['text'].push(gamelog.actionText + ' ' + player(gamelog, false))
      }
    }
    if (gamelog.homeTeamActionLCID === 'bFeldFehler' || gamelog.homeTeamActionLCID === 'b7mFehler' || gamelog.awayTeamActionLCID === 'bParade') {
      techH['x'].push(gamelog.timeInt)
      techH['y'].push(results[0])
      if (gamelog.homeTeamActionLCID === 'bFeldFehler') {
        techH['text'].push(gamelog.actionText + ' ' + player(gamelog, true))
        techH['marker']['symbol'].push('diamond')
      } else {
        techH['text'].push(gamelog.actionText + ' ' + player(gamelog, true) + ' (' + player(gamelog, false) + ')')
        techH['marker']['symbol'].push('cross')
      }
    }
    if (gamelog.awayTeamActionLCID === 'bFeldFehler' || gamelog.awayTeamActionLCID === 'b7mFehler' || gamelog.homeTeamActionLCID === 'bParade') {
      techA['x'].push(gamelog.timeInt)
      techA['y'].push(results[1])
      if (gamelog.awayTeamActionLCID === 'bFeldFehler') {
        techA['text'].push(gamelog.actionText + ' ' + player(gamelog, false))
        techA['marker']['symbol'].push('diamond')
      } else {
        techA['text'].push(gamelog.actionText + ' ' + player(gamelog, false) + ' (' + player(gamelog, true) + ')')
        techA['marker']['symbol'].push('cross')
      }
    }
    if (gamelog.homeTeamActionLCID === 'bTF') {
      techH['x'].push(gamelog.timeInt)
      techH['y'].push(results[0])
      techH['text'].push(gamelog.actionText + ' ' + player(gamelog, true))
      techH['marker']['symbol'].push('square')
    }
    if (gamelog.awayTeamActionLCID === 'bTF') {
      techA['x'].push(gamelog.timeInt)
      techA['y'].push(results[1])
      techA['text'].push(gamelog.actionText + ' ' + player(gamelog, false))
      techA['marker']['symbol'].push('square')
    }
    if (gamelog.homeTeamActionLCID === 'bTO') {
      eventH['x'].push(gamelog.timeInt)
      eventH['y'].push(30)
      eventH['text'].push(gamelog.actionText + ' Heim')
      eventH['marker']['symbol'].push('circle')
    }
    if (gamelog.awayTeamActionLCID === 'bTO') {
      eventA['x'].push(gamelog.timeInt)
      eventA['y'].push(30)
      eventA['text'].push(gamelog.actionText + ' Gast')
      eventA['marker']['symbol'].push('circle')
    }
  })

  return {
    data: [toreH, toreA, techH, techA, strafenH, strafenA, eventH, eventA]
  }
}

export default function Template({
  data, children // this prop will be injected by the GraphQL query below.
}) {
  const { gamelogJson } = data // data.mdx holds your post data

  let d = parseGamelog(gamelogJson)
  return (
    <Layout>
      <Section>
        <Container>
          <MDXProvider components={components}>{children}</MDXProvider>
          {gamelogJson.data.gameLog.map((gamelog, index) => {
            if ((gamelog.homeTeamActionLCID !== 'bFeldTor') &&
                (gamelog.awayTeamActionLCID !== 'bFeldTor') &&
                (gamelog.homeTeamActionLCID !== 'b7mTor') &&
                (gamelog.awayTeamActionLCID !== 'b7mTor') &&
                (gamelog.homeTeamActionLCID !== 'b2Min') &&
                (gamelog.awayTeamActionLCID !== 'b2Min') &&
                (gamelog.homeTeamActionLCID !== 'bFeldFehler') &&
                (gamelog.awayTeamActionLCID !== 'bFeldFehler') &&
                (gamelog.homeTeamActionLCID !== 'bVerwarnung') &&
                (gamelog.awayTeamActionLCID !== 'bVerwarnung') &&
                (gamelog.homeTeamActionLCID !== 'bTF') &&
                (gamelog.awayTeamActionLCID !== 'bTF') &&
                (gamelog.homeTeamActionLCID !== 'bTO') &&
                (gamelog.awayTeamActionLCID !== 'bTO') &&
                (gamelog.homeTeamActionLCID !== 'b7mFehler') &&
                (gamelog.awayTeamActionLCID !== 'b7mFehler') &&
                (gamelog.homeTeamActionLCID !== 'bParade') &&
                (gamelog.awayTeamActionLCID !== 'bParade')) {
              return (
                <div>
                  <span>({gamelog.timeInt}: {gamelog.homeTeamActionLCID} {gamelog.awayTeamActionLCID})|{gamelog.homeTeamPlayerStaffName}:{gamelog.awayTeamPlayerStaffName}</span>
                </div>
              )
            }
          })}
          <LazyPlot {...d } />
        </Container>
      </Section>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($gameId: Int!) {
    gamelogJson(gameId: { eq: $gameId }) {
      id
      data {
        gameLog {
          gameId
          actionGroup
          _xtypename
          actionText
          awayTeamActionLCID
          awayTeamPlayerStaffName
          awayTeamPlayerStaffNrDesc
          entryId
          homeTeamActionLCID
          homeTeamPlayerStaffName
          homeTeamPlayerStaffNrDesc
          language
          result
          timeInt
          timeString
        }
      }
    }
  }
`
